import React, { useState, useEffect, useRef } from 'react';
import { View, Image, Button, Text } from '@tarojs/components';
import Taro, { showToast, useRouter } from '@tarojs/taro';
import { getStorage, setStorage } from '@/utils/tools';

import './channel-item.scss';
import { api_user_access } from '@/api/account/account';
import { ITodayContent } from '@/api/content/content.i';
import { getToken, httpCheckLogin } from '@/utils/login';
import { IUserAccess } from '@/api/account/account.i';
import { api_template_subscription } from '@/api/mina_app/mina_app';

export type ChannelProps = {
  todayContent: ITodayContent.TodayContent[];
  channelInfo: ITodayContent.ChannelInfo;
  bookInfo: ITodayContent.BookInfo;
  studiedDay: number;
  hasJoin: Boolean;
  isSubscribed?: boolean;
};

const ChannelItem = (props: ChannelProps) => {
  const { todayContent, channelInfo, bookInfo, studiedDay, hasJoin } = props;
  const [isDisabled, setIsDisabled] = useState(false);
  const route = useRouter();
  const subscribeList = useRef<string[]>([
    '9-P1Vuc8OOuhg8CBLIOFWfjd1FsC2aLXtGNO1sWEcG4',
    '8LumSmpu4cipHE31pXG6KPn5QHi8aG1WzVxj710o-Kg',
    'ofoXGPaa5O5rdKshDwwlaTvwoMol1CuVbSrw8_GswA8',
  ]); // 订阅列表

  useEffect(() => {}, []);

  const bindToPlay = async (e) => {
    const { object_id, object_type, channel_id } = e.currentTarget.dataset.item;
    const { week_num } = channelInfo;
    await toSubscribe();
    let url = `/pages/detail/detail?object_id=${object_id}`;
    if (object_type === 'form') {
      url = `/pages/exam/exam?channel_id=${channel_id}&week_num=${week_num}`;
    }
    Taro.navigateTo({
      url,
    });
  };

  const bindJoin = async () => {
    if (!isDisabled) {
      setIsDisabled(true);
      const account_info = Taro.getStorageSync('account');
      if (account_info) {
        await toSubscribe();
        const source = route.params.user_type ?? (route.params.gift ? 'gift' : 'normal');
        getUserAccess({ source });
        return;
      }
      getUserProfile();
    } else {
      Taro.showToast({
        title: '操作太快啦！',
        icon: 'none',
        mask: true,
      });
    }
    setTimeout(() => {
      setIsDisabled(false);
    }, 500);
  };

  /** 订阅消息 */
  const toSubscribe = async (): Promise<boolean> => {
    const subscriptionMsg = getStorage('subscriptionMsg');
    return (
      !props.isSubscribed &&
      !subscriptionMsg &&
      new Promise((resolve) => {
        wx.requestSubscribeMessage({
          tmplIds: subscribeList.current,
          success(res) {
            const subList: string[] = []; // 已经订阅的列表
            subscribeList.current.forEach((sub) => {
              if (res?.[sub] === 'accept') {
                subList.push(sub);
              }
            });
            console.log('已订阅成功', subList);
            if (subList.length) {
              // 订阅消息发送
              api_template_subscription({ template_ids: subList });
            }
            setStorage('subscriptionMsg', 24 * 60 * 60 * 1000, { expires: 24 * 60 * 60 * 1000 });
            resolve(true);
          },
          fail(error) {
            console.error('订阅失败', error);
            resolve(false);
            return error;
          },
          complete() {},
        });
      })
    );
  };

  /**
   * @description: 请求加入
   * @param {*} option
   * @return {*}
   */
  const getUserAccess = async (option) => {
    try {
      const res = await api_user_access(option);
      if (res) {
        Taro.hideLoading();
        Taro.showToast({
          title: '加入成功，请开始你的听书之旅吧！',
          icon: 'none',
          duration: 2000,
          mask: true,
        });
        setTimeout(() => {
          Taro.navigateTo({
            url: '/pages/channel-plan/channel-plan',
          });
        }, 2000);
      }
    } catch (error) {
      console.error('getUserAccess error', error);
      Taro.hideLoading();
    }
  };

  const getUserProfile = () => {
    Taro.getUserProfile({
      desc: '用于用户身份辨别',
      success: (res) => {
        getCheckLogin(res);
      },
      fail: (err) => {
        console.log('授权失败：', err);
      },
    });
  };

  const getCheckLogin = (res) => {
    Taro.showLoading();
    const option = {
      encryptedData: res.encryptedData,
      iv: res.iv,
    };
    httpCheckLogin(option)
      .then(() => {
        Taro.showToast({ title: '请再次点击按钮~', icon: 'none' });
      })
      .catch(() => {
        Taro.hideLoading();
      });
  };

  return (
    <View className="content-container">
      {hasJoin ? (
        <>
          <View className="content-wrapper">
            {todayContent.map((item, _) => {
              return (
                <View className="content-item_box" key={item.object_id}>
                  <View className="title">{channelInfo?.name}</View>
                  <View className="book-cover">
                    <View className="tip-img"></View>
                    <Image
                      className="book-img"
                      mode="aspectFill"
                      src={item.object_type === 'form' ? item.json_extend.cover_url : bookInfo?.[item.book_id]?.cover_url ?? ''}></Image>
                  </View>
                  <View className="sub-title">{item.object_type === 'form' ? '一周小测' : item.day_num ? item.title : '先导课'}</View>
                  <View className="end-text">
                    <Text>加油，已坚持</Text>
                    <Text className="strong">{studiedDay}</Text>
                    <Text>天</Text>
                  </View>
                  <Button className="footer-button" onClick={bindToPlay} data-item={item} hoverClass="hover-class">
                    立即开始
                  </Button>
                </View>
              );
            })}
          </View>
        </>
      ) : (
        <>
          <View className="content-wrapper">
            <View className="content-item_box">
              <View className="title">{channelInfo?.noaccess_channel_title}</View>
              <View className="book-cover">
                <View className="tip-img"></View>
                <Image className="book-img" mode="aspectFill" src={channelInfo?.noaccess_channel_cover ?? ''}></Image>
              </View>
              <View className="sub-title">{channelInfo?.noaccess_channel_subtitle}</View>
              <View className="sub-title">{channelInfo?.noaccess_channel_description}</View>
              <Button className="footer-button" onClick={bindJoin} hoverClass="hover-class">
                立即加入
              </Button>
            </View>
          </View>
        </>
      )}
      {/*  新用户展示  */}
      {!(hasJoin && !todayContent.length) && (
        <View className="content-wrapper">
          <View className="content-item_box seize">
            <View className="light-title">更多精彩内容持续更新中</View>
            <View className="book-cover">
              <View className="tip-img"></View>
              <Image
                className="book-img"
                mode="aspectFill"
                src="https://static-1253442168.file.myqcloud.com/xupload/20210727_11.png"></Image>
            </View>
          </View>
        </View>
      )}
    </View>
  );
};

export default ChannelItem;
