import React, { useState, useEffect } from 'react';
import { View, Image, Text, Button } from '@tarojs/components';
import NavBar from '@/components/navbar/navbar';
import PageLoading from '@/components/loading/loading';
import { observer } from 'mobx-react';
import Taro, { useShareAppMessage, useRouter, useDidShow } from '@tarojs/taro';
import { ApiStore } from '@/store';
import ChannelItem from './components/channel-item/channel-item';
import { api_noaccess_content_page, api_today_content } from '@/api/content/content';

import './home.scss';
import { ITodayContent } from '@/api/content/content.i';
import { getToken } from '@/utils/login';
import { api_check_subscribed, api_judge_subscribed, api_judge_user_access } from '@/api/account/account';
import PlayerFloat from '@/components/player-float/player-float';
import Empty from '@/components/empty/empty';
import { track } from '@/utils/track';

const HomePage = observer(() => {
  const [loading, setLoading] = useState(true);
  const [hasJoin, setHasJoin] = useState(false); // 是否已经加入
  const [todayContent, setTodayContent] = useState<ITodayContent.TodayContent[]>([]); // 今日课程数据
  const [bookInfo, setBookInfo] = useState<ITodayContent.BookInfo>({});
  const [channelInfo, setChannelInfo] = useState<ITodayContent.ChannelInfo>();
  const [studiedDay, setStudiedDay] = useState(0); // 已学习天数
  const [isSubscribed, setIsSubscribed] = useState(false); // 是否关注公众号

  const route = useRouter();

  useShareAppMessage(() => {
    return ApiStore.common.defaultShareInfo;
  });

  useDidShow(async () => {
    console.log('home did show', route.params);
    checkIsNewUser();
    track.setEvent('lh_home_show');
  });

  /**
   * @description: 是否关注公众号
   * @param {*}
   * @return {*}
   */
  const checkHasSubscribed = async () => {
    const account_id = Number(Taro.getStorageSync('account').id ?? '');
    try {
      const res = await api_check_subscribed(account_id);
      if (res) {
        setIsSubscribed(res.is_subscribed);
      }
    } catch (error) {
      console.error('api_judge_subscribed error', error);
    }
  };

  const checkIsNewUser = () => {
    getToken(
      async () => {
        checkHasSubscribed();
        const accessStatus = Taro.getStorageSync('setIsAccess');
        if (accessStatus === 'join') {
          getTodayContent();
        } else {
          try {
            const res = await api_judge_user_access();
            if (res) {
              Taro.setStorageSync('setIsAccess', res.is_access ? 'join' : null);
              res.is_access ? getTodayContent() : getNoaccessContent();
            }
          } catch (error) {
            console.error('api_judge_user_access error', error);
          }
        }
      },
      () => {
        getNoaccessContent();
        setIsSubscribed(false);
      }
    );
  };

  /**
   * @description: 获取已加入的用户接口数据
   * @param {*}
   * @return {*}
   */
  const getTodayContent = async () => {
    try {
      const res = await api_today_content();
      if (res) {
        setTodayContent(res.today_content);
        setChannelInfo(res.channel_info);
        setBookInfo(res.book_info);
        setStudiedDay(res.studied_day);
        setHasJoin(true);
        Taro.setStorageSync('setChannelId', res.channel_info.channel_id);
      }
      setTimeout(() => {
        setLoading(false);
      }, 300);
    } catch (error) {
      setLoading(false);
      console.error('getTodayContent error', error);
    }
  };

  /**
   * @description: 获取无token和未加入用户接口数据
   * @param {*}
   * @return {*}
   */
  const getNoaccessContent = async () => {
    try {
      const res = await api_noaccess_content_page();
      if (res) {
        setTimeout(() => {
          setLoading(false);
        }, 300);
        setHasJoin(false);
        setChannelInfo(res);
      }
    } catch (error) {
      console.error('getNoaccessContent error', error);
    }
  };

  return loading ? (
    <PageLoading></PageLoading>
  ) : (
    <>
      <NavBar
        {...{
          title: '今日课程',
          setBgImage: 'https://staticqc-operating.lycheer.net/image/weike-cjPjaZ-1471964848.png',
          white: true,
          noBorder: true,
          hiddenBack: true,
        }}
      />
      {!todayContent.length && hasJoin ? (
        <Empty />
      ) : (
        <View>
          <View className="header-bg-wrapper">
            <View className="flex">
              <View className="flex">
                <Image className="header-logo" src="https://static-1253442168.file.myqcloud.com/xupload/20210721_logo.png"></Image>
                <View>
                  <Image className="text-logo" src="https://static-1253442168.file.myqcloud.com/xupload/20210721_title.png"></Image>
                  <View className="sub-text">陪伴女性终生成长</View>
                </View>
              </View>
              <Button className="button-share flex" openType="share">
                <Text className="icon icon-fenxiang"></Text>
                <Text className="share-text">分享</Text>
              </Button>
            </View>
          </View>
        </View>
      )}
      <ChannelItem
        todayContent={todayContent}
        bookInfo={bookInfo}
        channelInfo={channelInfo as ITodayContent.ChannelInfo}
        studiedDay={studiedDay}
        hasJoin={hasJoin}
        isSubscribed={isSubscribed}></ChannelItem>
      <PlayerFloat></PlayerFloat>
    </>
  );
});

export default HomePage;
